.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.3);
  position: fixed;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.25s, opacity 0.25s ease-in-out;
}

.modal_opened {
  visibility: visible;
  opacity: 1;
}

.modal__container {
  padding: 28px 28px 36px 28px;
  background-color: #fff;
  border-radius: 20px;
  position: relative;
}

.modal__fieldset {
  padding: 0;
  border: none;
  width: 440px;
  margin-top: 24px;
}

.modal__label {
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 20px;
}

.modal__input {
  display: block;
  width: 100%;
  background-color: transparent;
  border: none;
  border-color: #000;
  border-bottom: 1px solid #000;
  margin-top: 8px;
  padding-top: 5px;
  padding-bottom: 5px;
}

.modal__input::placeholder {
  font-size: 16px;
  line-height: 20px;
}

.modal__input:focus {
  border: none;
}

.modal__radio {
  margin-left: 0;
  margin-right: 11px;
  padding: 0;
}

.modal__heading {
  padding: 0;
  margin: 0 0 4px 0;
  font-size: 16px;
  line-height: 20px;
}

.modal__input-container {
  margin-top: 8px;
}

.modal__label_radio {
  color: rgba(0, 0, 0, 0.5);
}

.modal__label_radio_active {
  color: rgb(0, 0, 0);
}

.modal__button {
  margin-top: 32px;
  font-size: 16px;
  line-height: 20px;
  color: #fff;
  padding: 8px 16px;
  background-color: rgb(0, 0, 0, 0.3);
  border-radius: 4px;
  border: none;
  cursor: pointer;
  transition: 0.4s ease-in-out;
}

.modal__close-button {
  font-size: 16px;
  font-stretch: wider;
  color: rgba(0, 0, 0, 0.3);
  background-color: transparent;
  border: none;
  position: absolute;
  top: 20px;
  right: 28px;
  cursor: pointer;
}

@media screen and (max-width: 525px) {
  .modal__fieldset {
    width: 320px;
  }
}

@media screen and (max-width: 405px) {
  .modal__fieldset {
    width: 240px;
  }
}
