.header {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.header__left-column {
  display: flex;
  align-items: center;
}

.header__right-column {
  display: flex;
  align-items: center;
}

.header__logo {
  width: 89px;
  height: 40px;
}

.header__date-location {
  margin-left: 24px;
  font-size: 20px;
  font-weight: 700;
}

.header__username {
  font-size: 20px;
  font-weight: 700;
  margin-right: 12px;
}

.header__add-clothes-button {
  font-size: 20px;
  border: none;
  background-color: transparent;
  margin-right: 20px;
  font-weight: 700;
  cursor: pointer;
  transition: 0.25s ease-in-out;
}

.header__add-clothes-button:hover {
  color: rgb(125, 123, 123);
}

.header__profile {
  display: flex;
  align-items: center;
}

.header__avatar {
  border-radius: 50%;
  width: 40px;
  height: 40px;
  font-weight: 700;
}

@media screen and (max-width: 870px) {
  .header__left-column {
    flex-direction: column;
  }

  .header {
    flex-direction: column;
  }

  .header__right-column {
    justify-content: space-between;
  }
}
